.indigred_item{
  width: calc((100% - 20px) / 3);
  a {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  p {
    text-align: center;
		font-size: 1.6rem;
  }
  // img {
  //   width: 80%;
  //   padding: 10px;
  // }
  .item_inner {
		position: relative;
		width: 70%;

		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: relative;
			padding-bottom: 100%;
		}
	}

	.ingredi_img {
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		// background-image: url(../../common/Loading_Spin-1s-200px.svg);
		// background-size: 100%;
		// background-position: center;
		// background-repeat: no-repeat;
	}
} 